import React from 'react';
import { Nav, NavItem, NavDropdown, MenuItem} from 'react-bootstrap';

export default class Navigation extends React.Component {

  render() {
    return (
      <div>
        <Nav bsStyle="pills" justified >
          <NavItem href='/'>Home</NavItem>
          <NavDropdown title="About Me" id="nav-dropdown">
            <MenuItem >Introduction</MenuItem>
            <MenuItem href='/about/colorfullift'>Colorful Life</MenuItem>
            <MenuItem href='/about/favourites'>Favourites</MenuItem>
            <MenuItem divider />
            <MenuItem href='/about/introduction'>Resume</MenuItem>
          </NavDropdown>
          <NavItem title="Item">Others</NavItem>
        </Nav>
      </div>
    );
  }
}




